body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.loginForm{
  width: 50vw;
  margin-top: 10vw;
}
.memberLogin{
  display: flex;
  align-items: center;
  justify-content: center;
}
#link{
  text-decoration: none;
  color: white;
}

.registeredMember{
  margin-top: 5vw;
}
.listData p{
 margin: 0;
 padding: 0;
}

.dataCard{
  min-width: 200px;
  width: 25vw;
  margin: 2vw;
}
.memberData{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.dataCard>img{
  height: 50%;
}
.memberCard{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10vw;
 
}

.detailCard{
  width: 70vw;
  display: flex;
  flex-direction: row;

}


.card-img, .card-img-bottom, .card-img-top {
    width: 50%;
}
#logOut{
  text-decoration: none;
  color: white;
}
.errorPage{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
margin-top: 10vw;
}
.errorPage h3{
  font-size: 15vw;
}